* {
	font-family: inherit;
	font-size: inherit;
	line-height: inherit;
}

*,
*::before,
*::after {
	box-sizing: inherit;
}

html {
	// 1rem = 10px; 10px/16px = 62.5%;
	// https://snook.ca/archives/html_and_css/font-size-with-rem
	font-size: 62.5%;
}

body {
	box-sizing: border-box;
}

svg {
	display: inline-block;
}

a,
abbr,
address,
article,
aside,
audio,
blockquote,
body,
canvas,
caption,
cite,
code,
dd,
del,
details,
dfn,
div,
dl,
dt,
em,
fieldset,
figcaption,
figure,
footer,
form,
h1,
h2,
h3,
h4,
h5,
h6,
header,
hgroup,
html,
iframe,
img,
ins,
label,
legend,
li,
main,
mark,
nav,
ol,
p,
pre,
q,
section,
small,
span,
strike,
strong,
sub,
summary,
sup,
table,
tbody,
td,
tfoot,
th,
thead,
time,
tr,
ul,
video {
	margin: 0;
	border: none;
	padding: 0;
}

a,
a:visited {
	color: inherit;
}

article,
aside,
footer,
header,
nav,
section,
main {
	display: block;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

ol,
ul {
	list-style: none;
}

img,
video {
	max-width: 100%;
}

img {
	border-style: none;
}

blockquote,
q {
	quotes: none;
}

blockquote::after,
blockquote::before,
q::after,
q::before {
	content: none;
}

[hidden] {
	/* stylelint-disable-next-line declaration-no-important */
	display: none !important;
}

[disabled] {
	cursor: not-allowed;
}

:focus:not(:focus-visible) {
	outline: none;
}
