.modal-overlay {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 3;
  background-color: rgba(get-color('base900'), 0.55);
  overflow-x: hidden;
  overflow-y: auto;
}

.modal {
  display: flex;
  width: 100%;
  padding: 3.2rem;
  justify-content: center;

  .arrows {
    .prev,
    .next {
      display: flex;
      width: 4rem;
      height: 4rem;
      margin-top: -2rem;
      position: absolute;
      top: 50%;
      align-items: center;
      justify-content: center;
      font-size: 2rem;
      cursor: pointer;

      &:active,
      &:active {
        color: get-color('accent500');
      }
    }

    .prev {
      left: 0;
    }

    .next {
      right: 0;
    }
  }
}

.modal-wrapper {
  width: 100%;
  border-radius: 0.8rem;
  padding: 3.2rem;
  position: relative;
  background-color: get-color('white');
  box-shadow: 0 0.6rem 0.6rem 0 rgba(0, 20, 32, 0.09),
    0 1rem 2rem 0 rgba(0, 20, 32, 0.13);
}

modal-clab[classic] .modal-wrapper {
  max-width: 80rem;
}

.close-overlay {
  position: absolute;
  top: 0.8rem;
  right: 0.8rem;
}

.modal-title {
  @extend h2;
}

.modal-info {
  display: grid;
  margin-bottom: 2.4rem;
  grid-template-rows: repeat(2, auto);
  grid-auto-columns: max-content;
  grid-auto-flow: column;
  grid-gap: 1.6rem 3.2rem;
  line-height: 1;

  li {
    display: flex;
    margin: 0;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: flex-start;
  }

  li > span:first-child {
    padding-right: 0.4rem;
    color: get-color('base500');
  }
}

.modal-body {
  margin-bottom: 2.4rem;
}
