@import './colors';

$font-stack: 'Montserrat', sans-serif;

body {
	color: get-color('base900');
	font: normal normal 1.4rem/1.5em $font-stack;
}

h1,
h2,
h3,
h4,
h5,
h6,
.hero-text {
	color: get-color('base800');
	font-weight: bold;
	text-rendering: optimizeLegibility;
}

.hero-text {
	margin-bottom: 2.6rem;
	font-size: 6.4rem;
	line-height: 1em;
}

%hero-text {
	margin-bottom: 2.6rem;
	font-size: 6.4rem;
	text-rendering: optimizeLegibility;
	line-height: 1em;
}

h1 {
	margin-bottom: 2.4rem;
	font-size: 3rem;
	line-height: 1.2em;
}

h2 {
	margin-bottom: 2.2rem;
	font-size: 2.8rem;
	line-height: 1.2em;
}

h3 {
	margin-bottom: 2rem;
	font-size: 2.6rem;
	line-height: 1.2em;
}

h4 {
	margin-bottom: 1.8rem;
	font-size: 2.4rem;
	line-height: 1.4em;
}

h5 {
	margin-bottom: 1.6rem;
	font-size: 2.2rem;
	line-height: 1.4em;
}

h6 {
	margin-bottom: 1.4rem;
	font-size: 2rem;
	line-height: 1.4em;
}

.subheading-1,
.subheading-2,
.subheading-3 {
	color: get-color('base700');
	font-weight: normal;
	text-rendering: optimizeLegibility;
	line-height: 1.3em;
}

.subheading-1 {
	display: block;
	margin-bottom: 1.6rem;
	font-size: 2.2rem;
}

%subheading-1 {
	display: block;
	margin-bottom: 1.6rem;
	color: get-color('base700');
	font-size: 2.2rem;
	font-weight: normal;
	text-rendering: optimizeLegibility;
	line-height: 1.3em;
}

.subheading-2 {
	display: block;
	margin-bottom: 1.4rem;
	font-size: 2rem;
}

%subheading-2 {
	display: block;
	margin-bottom: 1.4rem;
	color: get-color('base700');
	font-size: 2rem;
	font-weight: normal;
	text-rendering: optimizeLegibility;
	line-height: 1.3em;
}

.subheading-3 {
	display: block;
	margin-bottom: 1.2rem;
	font-size: 1.8rem;
}

%subheading-3 {
	display: block;
	margin-bottom: 1.2rem;
	color: get-color('base700');
	font-size: 1.8rem;
	font-weight: normal;
	text-rendering: optimizeLegibility;
	line-height: 1.3em;
}

p {
	margin-bottom: 1.4rem;
}

mark {
	border-radius: 2px;
	padding: 1.5px 4px;
	color: get-color('white');
	background-color: get-color('accent500');
}

small {
	color: get-color('accent600');
	font-size: 1.2rem;
	line-height: 1.4em;
}
